import $api from "../http";
import { AxiosResponse } from "axios";
import { IAuthResponse } from "../models/response/AuthResponse";
export default class AuthService {
  static async login(
    email: string,
    password: string
  ): Promise<AxiosResponse<IAuthResponse>> {
    return $api.post<IAuthResponse>("/api/login", {
      email,
      password,
    });
  }
  static async registration(
    email: string,
    password: string,
    name: string,
    surname: string
  ): Promise<AxiosResponse<IAuthResponse>> {
    return $api.post<IAuthResponse>("/api/registration", {
      email,
      password,
      name,
      surname,
    });
  }
  static async loguot(email: string, password: string): Promise<void> {
    return $api.post("/api/logout");
  }
  static async checkAuth(): Promise<void> {
    return $api.get("/api/refresh");
  }
}
